import { Box, Heading, Image, SimpleGrid } from "@chakra-ui/react";

export default function Skills() {
  return (
    <>
      <Heading mt={7} mb={7}>
        Frontend
      </Heading>
      <SimpleGrid
        w="70%"
        gap={5}
        margin="auto"
        columns={{ lg: 5, md: 4, sm: 3, base: 2 }}
      >
        <Box w="130px" borderRadius={"20px"} p="20px" background={"#17252A"}>
          <Image
            src="data:image/png;base64,"
            w={"110px"}
            h={"110px"}
          />
        </Box>
        <Box w="130px" borderRadius={"20px"} p="20px" background={"#17252A"}>
          <Image
            src="data:image/png;base64,"
            w={"100px"}
            h={"110px"}
          />
        </Box>
        <Box w="130px" borderRadius={"20px"} p="20px" background={"#17252A"}>
          <Image
            src="data:image/png;base64,"
            w={"100px"}
            h={"110px"}
          />
        </Box>
        <Box w="130px" borderRadius={"20px"} p="20px" background={"#17252A"}>
          <Image
            src="data:image/png;base64,"
            w={"100px"}
            h={"110px"}
          />
        </Box>
        <Box w="130px" borderRadius={"20px"} p="20px" background={"#17252A"}>
          <Image
            src="data:image/png;base64,"
            w={"100px"}
            h={"110px"}
          />
        </Box>
      </SimpleGrid>
      <Heading mt={7} mb={7}>
        Backend
      </Heading>
      <SimpleGrid
        w="70%"
        gap={5}
        margin="auto"
        columns={{ lg: 4, md: 3, sm: 2, base: 2 }}
      >
        <Box w="150px" borderRadius={"20px"} p="20px" background={"#17252A"}>
          <Image
            src="data:image/png;base64,"
            w={"150px"}
            h={"110px"}
          />
        </Box>
        <Box w="180px" borderRadius={"20px"} p="20px" background={"#17252A"}>
          <Image
            src="data:image/png;base64,"
            w={"180px"}
            h={"110px"}
          />
        </Box>
      </SimpleGrid>
      <Heading mt={7} mb={7}>
        Tools
      </Heading>
      <SimpleGrid
        w="70%"
        gap={5}
        margin="auto"
        columns={{ lg: 4, md: 3, sm: 2, base: 2 }}
      >
        <Box w="130px" borderRadius={"20px"} p="20px" background={"#17252A"}>
          <Image
            src="data:image/jpeg;base64,"
            w={"100px"}
            h={"110px"}
          />
        </Box>
        <Box w="130px" borderRadius={"20px"} p="20px" background={"#17252A"}>
          <Image
            src="data:image/png;base64,"
            w={"100px"}
            h={"110px"}
          />
        </Box>
      </SimpleGrid>
    </>
  );
}
