// import { ReactNode } from "react";
import {
  Box,
  Flex,
  Avatar,
  HStack,
  IconButton,
  Button,
  Menu,
  MenuButton,
  useDisclosure,
  useColorModeValue,
  Stack,
} from "@chakra-ui/react";
// import { NavLink } from "react-router-dom";
import { Link } from "react-scroll";
import { HamburgerIcon, CloseIcon } from "@chakra-ui/icons";

// const Links = ["About", "Projects", "Skills", "Contact Me"];
const Links = [
  { title: "About", path: "about" },
  { title: "Skills", path: "skill" },
  { title: "Projects", path: "project" },
  { title: "Contact", path: "contact" },
  {
    title: "Resume",
    path: "https://drive.google.com/file/d/1U9pKGFa6VbSpQNZ1h20XwmGdLEu6TE3i/view?usp=share_link",
  },
];

export default function Navbar() {
  const { isOpen, onOpen, onClose } = useDisclosure();

  return (
    <Box
      zIndex={5}
      position="sticky"
      top="0"
      bg={useColorModeValue("#17252A", "#17252A")}
      px={4}
      w={{ md: "100%", sm: "100%", lg: "100%" }}
      margin={{ md: "auto", base: "auto" }}
    >
      <Flex h={16} alignItems={"center"} justifyContent={"space-between"}>
        <IconButton
          size={"md"}
          icon={isOpen ? <CloseIcon /> : <HamburgerIcon />}
          aria-label={"Open Menu"}
          display={{ md: "none" }}
          onClick={isOpen ? onClose : onOpen}
        />
        <HStack spacing={8} alignItems={"center"}>
          <Box
            borderRadius="10px"
            p="5px 20px"
            fontSize="20px"
            fontWeight="bold"
            color="#3AAFA9"
            border="1px double white"
          >
            Nikhil
          </Box>
          <HStack as={"nav"} spacing={4} display={{ base: "none", md: "flex" }}>
            {Links.map((el) => (
              <Button
                background={"#17252A"}
                color={"white"}
                _hover={{ color: "#3AAFA9", background: "white" }}
                key={el.title}
              >
                {el.path ===
                "https://drive.google.com/file/d/1U9pKGFa6VbSpQNZ1h20XwmGdLEu6TE3i/view?usp=share_link" ? (
                  <a
                    target={"_blank"}
                    href="https://drive.google.com/file/d/1U9pKGFa6VbSpQNZ1h20XwmGdLEu6TE3i/view?usp=share_link"
                  >
                    {el.title}
                  </a>
                ) : (
                  <Link smooth={true} duration={1000} to={el.path}>
                    {el.title}
                  </Link>
                )}
              </Button>
            ))}
          </HStack>
        </HStack>
        <Flex alignItems={"center"}>
          <Menu>
            <MenuButton
              as={Button}
              rounded={"full"}
              variant={"link"}
              cursor={"pointer"}
              minW={0}
            >
              <Avatar
                size={"sm"}
                src="data:image/jpeg;base64,"
              />
            </MenuButton>
          </Menu>
        </Flex>
      </Flex>

      {isOpen ? (
        <Box pb={4} display={{ md: "none" }}>
          <Stack as={"nav"} spacing={4}>
            {Links.map((el) => (
              <Box
                borderRadius="10px"
                fontSize="20px"
                fontWeight="bold"
                color="#3AAFA9"
                border="1px double white"
                key={el.title}
              >
                <Link onClick={onClose} to={el.path}>
                  {el.title}
                </Link>
              </Box>
            ))}
          </Stack>
        </Box>
      ) : null}
    </Box>
  );
}
